  font-family: "Gilroy-SemiBold";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  font-weight: normal;
}
@font-face {
  font-family: "Gilroy-Regular";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  font-weight: normal;
}
.modalWindow {
  display: none;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  gap: 48px;
}

.modal-window__wrapper {
  margin: 5px auto;
  width: 540px;
  height: 800px;
  padding: 20px 56px;
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
  box-shadow: 5px 10px 50px 0px rgba(16, 112, 177, 0.2);
  z-index: 100;
}

.modal-window__wrapper--active {
  display: flex;
}

.upper__content {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  align-content: center;
  justify-content: space-between;
  margin-bottom: 20px;
  gap: 10px;
}

.close__button {
  font-family: "Gilroy-SemiBold";
  color: #1F3F68;
  cursor: pointer;
}

.modal-window__h2 {
  color: #1F3F68;
  font-family: "Gilroy-SemiBold";
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 30px;
  letter-spacing: 0.8px;
  text-transform: uppercase;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.form__item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form__input {
  padding: 21px 200px 21px 42px;
  border-radius: 10px;
  border: 1px solid #356EAD;
  opacity: 0.4;
  color: var(--Blue_01, #1F3F68);
  font-family: "Gilroy-Regular";
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 30px; /* 187.5% */
  letter-spacing: 0.64px;
}

.form__input-message {
  padding: 21px 200px 93px 36px;
}

.form__header {
  color: #1F3F68;
  font-family: "Gilroy-Regular";
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 30px; /* 187.5% */
  letter-spacing: 0.64px;
  opacity: 0.8;
}

.form__submit {
  border-radius: 10px;
  background: #5A98D0;
  box-shadow: 5px 20px 50px 0px rgba(16, 112, 177, 0.2);
  border: none;
  padding: 23px 184px;
  margin-top: 65px;
  color: #FFF;
  text-align: center;
  font-family: "Gilroy-SemiBold";
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 26px;
  letter-spacing: 0.64px;
}

.error {
  color: red;
  font-family: "Gilroy-SemiBold";
  font-size: 10px;
  text-align: center;
}

.error-name,
.error-email,
.error-message {
  display: none;
}

.error-visible {
  display: block;
}

@media (max-width: 548px) {
  .modal-window__wrapper {
    width: 90%;
  }
  .form__submit {
    padding: 23px 0;
  }
  .form__input {
    padding: 21px 10px 21px 42px;
  }
}
.modalWindowValide {
  display: none;
  flex-direction: column;
  padding-top: 25%;
  align-items: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  gap: 48px;
}

.modalWindowValide__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  margin: 10px;
}

.modalWindowValide__wrapper {
  padding: 20px;
  background-color: #FFF;
}

.modalWindowValide__wrapper h2 {
  color: green;
  font-family: "Gilroy-SemiBold";
}

.modalWindowValide-visible {
  display: flex;
